@import url(partials/reset.css);
@import url(partials/fonts.css);
@import url(partials/jquery.fancybox.min.css);
/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent; }

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0; }
  .slick-list:focus {
    outline: none; }
  .slick-list.dragging {
    cursor: pointer;
    cursor: hand; }

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0); }

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto; }
  .slick-track:before, .slick-track:after {
    content: "";
    display: table; }
  .slick-track:after {
    clear: both; }
  .slick-loading .slick-track {
    visibility: hidden; }

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none; }
  [dir="rtl"] .slick-slide {
    float: right; }
  .slick-slide img {
    display: block; }
  .slick-slide.slick-loading img {
    display: none; }
  .slick-slide.dragging img {
    pointer-events: none; }
  .slick-initialized .slick-slide {
    display: block; }
  .slick-loading .slick-slide {
    visibility: hidden; }
  .slick-vertical .slick-slide {
    display: block;
    height: auto;
    border: 1px solid transparent; }

.slick-arrow.slick-hidden {
  display: none; }

/* my style */
a {
  text-decoration: none; }

body {
  margin: 0;
  color: #3b3b3b;
  background: #ffffff;
  font-family: "Gilroy", sans-serif;
  font-size: 16px; }

.container {
  box-sizing: border-box;
  width: 1240px;
  max-width: calc(100% - 32px);
  margin: 0 auto;
  margin: 0 auto; }

#jGrowl {
  z-index: 999999; }

*:focus {
  outline: none; }

.hide {
  display: none; }

.btn {
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 10px 24px;
  border-radius: 6px;
  font-family: "Gotham Pro", sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: 125%;
  font-style: normal;
  font-weight: 500; }
  .btn--blue {
    color: #FFFFFF;
    background: #2A68FB; }

.site-header {
  background: linear-gradient(0deg, rgba(26, 30, 41, 0.74), rgba(26, 30, 41, 0.74)); }

.sh-top .container {
  display: flex;
  align-items: center; }

.sh-burger {
  position: relative;
  width: 24px;
  height: 18px;
  margin-right: 40px;
  cursor: pointer; }
  .sh-burger span,
  .sh-burger span::before,
  .sh-burger span::after {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 24px;
    height: 2px;
    margin-top: -1px;
    margin-left: -12px;
    background-color: #fff; }
  .sh-burger span::before,
  .sh-burger span::after {
    content: '';
    transition: 0.2s; }
  .sh-burger span::before {
    transform: translateY(-8px); }
  .sh-burger span::after {
    transform: translateY(8px); }
  .sh-burger--active span {
    height: 0; }
    .sh-burger--active span::before {
      transform: rotate(45deg); }
    .sh-burger--active span::after {
      transform: rotate(-45deg); }

.sh-logo {
  display: flex;
  align-items: center;
  width: 219px;
  margin-right: 60px; }
  .sh-logo img {
    width: 100%; }

.sh-btn-count-wrap {
  flex-grow: 1; }

.sh-call-me {
  margin-right: 32px;
  color: #00A7FF;
  font-family: "Gotham Pro", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 125%;
  font-style: normal; }

.sh-phone-number {
  color: #FFFFFF;
  font-family: "Gotham Pro", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 125%; }
